.cell {
	width: 10%;
	height: 100%;
	align-items: center;

	&_content {
		position: relative;
		width: 100%;
		height: 100%;
	}
}

.marker_green {
	background: url(../../images/green_rectangle.svg);
}

.marker_red {
	background: url(../../images/red_rectangle.svg);
}

.marker_blue {
	width: 100%;
	height: 100%;
	display: block;
	background: url(../../images/blue_rectangle.svg);
}

.marker {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0px -1px;
}

.hit {
	width: 100%;
	height: 100%;
	color: red;
	font-size: 25px;
	line-height: 30px;
	text-align: center;
	position: absolute;
	left: 0;
	top: 0;

	z-index: 3;
}

.animation {
	animation-name: miss;
	animation-duration: 0.6s;
	animation-iteration-count: 1;
	animation-timing-function: ease-in-out;
}

@keyframes miss {
	from {
		width: 0%;
		height: 0%;
	}

	to {
		width: 100%;
		height: 100%;
	}
}

@media (max-width: 1000px) {
	@media (max-height: 360px) {
		.hit {
			font-size: 22px;
			line-height: 25px;
		}
	}
}

@media (max-width: 360px) and (orientation: portrait) {
	.hit {
		font-size: 22px;
		line-height: 25px;
	}
}
